* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}
.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

}

.app__title {
  color: rgb(255, 0, 0);
  letter-spacing: 0.5px;
 font-size: 2rem;
}

.app__dropdown {
  background-color: white;
  border:2px tomato !important;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__graphTitle {
  margin-top:20px;
  margin-bottom: 20px;
}

.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__left{
  flex: 0.9;
}

@media(max-width:990px){
  .app{
    flex-direction: column;
  }
}

.app__footer {
  display: flex;
  flex-direction: row;
}

.app__right {
  display: flex;
  flex-direction: column;
}

.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

.app__information > h3 {
  color: #6a5d5d;
  font-weight: 400;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.app__information > h3:last-of-type {
  margin-top: 1rem;
}